* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

/* Footer */
footer {
  display: inline-flex;
  width: 100%;
  background-color: grey;
}

footer p {
  margin-right: 2em;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: red;
}

/* Details */
.price {
  width: 20%;
  float: right;
  font-style: italic;
  background-color: whitesmoke;
  border: 1px solid rgb(221, 221, 221);
  border-radius: 12px;
  padding: 14px;
  box-shadow: whitesmoke 0px 6px;
  text-align: center;
}

.description {
  width: 68%;
  float: left;
}
